<template>
  <div class="login-center">
    <section class="login-center-banner">
      <div class="login-center-user"><i class="mintui mintui-back return-icon" @click="setting('back')"></i></div>
      <div class="login-logo"><img src="/static/image/login/login_logo@3x.png"></div>
    </section>
    <section class="login-center-input">
      <div class="input-tel ">
        <img src="/static/image/login/login_icon_account@3x.png"></img><input type="number" v-model="tel" v-bind:value="tel" @focus="setColor(0)" @blur="removeColor(0)" placeholder="请输入手机号码" />
        <div v-bind:class="[line,focus[0].focus ? lineColor:line]"></div>
      </div>
      <div class="input-password">
        <img src="/static/image/login/login_icon_password@3x.png"></img><input type="password" autofocus="autofocus" v-model="password" v-bind:value="password" id="psw" autocomplete="off" @focus="setColor(1)" @blur="removeColor(1)" placeholder="密码" />
        <div v-bind:class="[line,focus[1].focus ? lineColor:line]"></div>
      </div>
      <div class="login-in" :class="status" @click="validate(custom)">登录</div>
      <span class="register" @click="setting(register,tel)">立即注册</span><span class="forget" @click=' setting(pswRouter,custom)'>忘记密码？</span>
    </section>
  </div>
</template>

<script>
  import {postLogin} from '../../service/getData'
  export default {
    data () {
      return {
        tel: '',
        password: '',
        text: 'forget',
        line: 'input-botn',
        isSubmit: false,
        lineColor: 'input-botn-color',
        focus: [{
          focus: false,
          name: 'tel'
        }, {
          focus: false
        }],
        custom: 'custom',
        register: 'register',
        forget: 'forgetpassword',
        pswRouter: 'forgetPsw'
      }
    },
    created: function () {
      console.log(localStorage.userMobile)
    },
    mounted: function () {
      let input = document.querySelector('#psw')
    },
    methods: {
      setting: function (val, param) {
        if (val === 'back') {
          this.$router.go(-1)
        } else {
          this.$router.push({
            name: val,
            params: {
              pageTitle: '找回密码',
              routerName: 'login'
            }
          })
        }
      },
      validate: function (val) {
        var _this = this
        if (!/^(\(\d{3,4}\)|\d{3,4}-|\s)?\d{7,14}$/.test(_this.tel)) {
          _this.$toast('请输入正确的手机号码')
          return
        }
        if(_this.password == '' || _this.password == ' '){
          _this.$toast('登录密码不能为空')
          return
        }
        postLogin(this.tel, this.password).then(function (rep) {
           if(rep.data.code == '1001'){
             _this.$toast('登录账号错误')
             return
           }else if(rep.data.code == '1002'){
             _this.$toast('密码错误')
             return
           }else if(rep.data.code == '1003'){
             _this.$toast('账号已被锁定')
             return
           }else if(rep.data.mobile != null){
             _this.$toast('登录成功')
             localStorage.setItem('userMobile', rep.data.mobile)
             localStorage.setItem('qrCode', rep.data.qrCode)
             //跳转到我的页面
             _this.$router.push({
                name: 'custom',
                params: {
                  routerName: 'login'
                  //joyScore: '110',
                  //loveHeart: rep.data.loveHeart
                }
             })
           }else{
             _this.$toast('登录失败，服务器返回数据异常！')
           }
        }).catch(function () {
            _this.$toast('登录失败，连接服务器异常！')
        })
      },
      setColor: function (item) {
        this.focus[item].focus = true
      },
      removeColor: function (item) {
        this.focus[item].focus = false
      }
    },
    computed: {
      status: function () {  //{'no-success':!isSubmit,success:isSubmit}
          if (this.tel == '' || this.password == '') {
            return 'no-success'
          } else {
            return 'success'
        }
      }
    }
  }
</script>

<style>
  /* 登录页面 */
  .login-center-banner {
    height: 1.62rem;
    position: relative;
    z-index: 1;
    padding: .2rem .125rem .4rem .125rem;
    background: url(/static/image/login/login_bg@3x.png);
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
  }
  
  .login-center {
    height: 100%;
    width: 100%;
    background-color: white;
  }
  
  .login-center-user {
    height: 100%;
    width: 96%;
    margin: 0 auto;
    display: flex;
    color: #FFFFFF;
    position: relative;
  }
  
  .return-icon {
    position: absolute;
    height: .18rem;
    width: .18rem;
    font-size: .18rem;
    line-height: .18rem;
    text-align: center;
  }
  
  .login-logo img {
    width: 1.10rem;
    height: 1.10rem;
    position: relative;
    left: 50%;
    transform: translate(-50%, -30%);
  }
  
  .input-tel {
    width: 100%;
    padding: .70rem .36rem .0rem .36rem;
  }
  
  .login-center-input img {
    width: .18rem;
    height: .18rem;
  }
  
  .input-tel input,
  .input-password input {
    width: 90%;
    font-size: .18rem;
    transform: translate(.05rem, -20%);
    -webkit-box-shadow: 0 0 0px 1000px white inset;
    background-color: #f5f5f5!important;
  }
  
  .input-botn {
    width: 100%;
    height: .01rem;
    background-color: rgba(6, 11, 0, .1);
    margin-top: .1rem;
  }
  
  .input-botn-color {
    background-color: rgba(255, 139, 32, .6);
  }
  
  .input-password {
    width: 100%;
    padding: .27rem .36rem .0rem .36rem;
  }
  
  .login-in {
    position: absolute;
    width: 80%;
    height: 7%;
    line-height: .45rem;
    left: 50%;
    margin-top: .3rem;
    text-align: center;
    color: rgb(255, 161, 38);
    transform: translateX(-50%);
    border-radius: .3rem;
    color: white;
  }
  .no-success{
    background: url(/static/image/login/login_bar_dis@3x.png) no-repeat;
    background-size: 100% 100%;
  }
  .success{
    background: url(/static/image/login/login_bar_nor@3x.png) no-repeat;
    background-size: 100% 100%;
  }
  
  .register {
    position: absolute;
    left: 10%;
    margin-top: .93rem;
    color: #ff8b20;
  }
  
  .forget {
    position: absolute;
    right: 10%;
    margin-top: .93rem;
    color: #ff8b20;
  }
</style>


